
.HomeImage{
    height:80%;
    width:80%;
    margin-top:10vh;
    padding:10px;
}

@import url('https://fonts.googleapis.com/css?family=Roboto:700');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');

.HomeHeadText {
  font-family: 'Lobster', cursive;
  font-size:60px;
  text-align:center;
  margin-top:30px;
  
}
.HomeDescription{
  font-family: 'Russo One', sans-serif;
  font-size:25px;
  margin:30px;
  margin-top:5px;
}
.TextAnim {
  color:#999;
  text-transform: uppercase;
  font-size:36px;
  font-weight:bold;
 font-family:'Roboto';
  text-align:center;
}

.flip {
  height:50px;
  overflow:hidden;
}

.flip > div > div {
  color:#fff;
  padding:4px 12px;
  height:50px;
  margin-bottom:45px;
  display:inline-block;
}

.flip div:first-child {
  animation: show 5s linear infinite;
}

.flip div div {
  background:#42c58a;
}
.flip div:first-child div {
  background:#4ec7f3;
}
.flip div:last-child div {
  background:#DC143C;
}

@keyframes show {
  0% {margin-top:-270px;}
  5% {margin-top:-180px;}
  33% {margin-top:-180px;}
  38% {margin-top:-90px;}
  66% {margin-top:-90px;}
  71% {margin-top:0px;}
  99.99% {margin-top:0px;}
  100% {margin-top:-270px;}
}

